
import { defineComponent, ref } from 'vue'
import { useUserAnswersStore } from '@/stores/userAnswers'
import { useTestsStore } from '@/stores/tests'
export default defineComponent({
  name: 'QuestionAndAnswers',
  props: {
    id: {
      type: Number,
      required: true
    },
    question: {
      type: String,
      required: true
    },
    answerA: {
      type: String,
      required: true
    },
    answerB: {
      type: String,
      required: true
    },
    answerC: {
      type: String,
      required: true
    },
    answerD: {
      type: String,
      required: true
    },
  },
  setup(props) {
    const storeAnswers = useUserAnswersStore()
    const { userAnswers, correctAnswers } = storeAnswers
    const testStore = useTestsStore()

    const chosenAnswers = ref({} as string)
    const userAnswer = ref()
    const selected = ref(userAnswers[props.id])

    const items = [
      {
        value: 'a',
        text: props.answerA
      },
      {
        value: 'b',
        text: props.answerB
      },
      {
        value: 'c',
        text: props.answerC
      },
      {
        value: 'd',
        text: props.answerD
      }
    ]

    const setValue = (ev: Event) => {
      userAnswer.value = ((ev.target as HTMLInputElement).value)
      chosenAnswers.value = Object.assign(userAnswers, { [props.id]: userAnswer })
    }

    return {
      selected,
      setValue,
      storeAnswers,
      testStore,
      userAnswers,
      correctAnswers,
      items
    }
  }
})
